@media (max-width: 424px) {
    .dev_wrapper .dev_info .dev_contact_cv .btn_cv{
        max-width: 50%;
    }
    .dev_wrapper .dev_info .dev_contact_cv .btn_contact{
        max-width: 50%;
    }
}
#notfound {
    position: relative;
    height: 100vh;
}

#notfound .notfound-bg {
    position: absolute;
    width: 100%;
    height: 100%;
    background-size: cover;
}

#notfound .notfound-bg:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(13, 17, 20, 0.7);
}

#notfound .notfound {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.notfound {
    max-width: 910px;
    width: 100%;
    line-height: 1.4;
    text-align: center;
}

.notfound .notfound-404 {
    position: relative;
    height: 200px;
}

.notfound .notfound-404 h1 {
    font-family: 'Montserrat', sans-serif;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    font-size: 220px;
    font-weight: 900;
    margin: 0px;
    color: #fff;
    text-transform: uppercase;
    letter-spacing: 10px;
}

.notfound h2 {
    font-family: 'Montserrat', sans-serif;
    font-size: 22px;
    font-weight: 700;
    text-transform: uppercase;
    color: #fff;
    margin-top: 20px;
    margin-bottom: 15px;
}

.notfound .home-btn {
    font-family: 'Montserrat', sans-serif;
    display: inline-block;
    font-weight: 700;
    text-decoration: none;
    background-color: transparent;
    border: 2px solid transparent;
    text-transform: uppercase;
    padding: 13px 25px;
    font-size: 18px;
    border-radius: 40px;
    margin: 7px;
    -webkit-transition: 0.2s all;
    transition: 0.2s all;
}

.notfound .home-btn:hover {
    opacity: 0.9;
}

.notfound .home-btn {
    color: #0AC379;
    background: #fff;
}
@media only screen and (max-width: 767px) {
    .notfound .notfound-404 h1 {
        font-size: 182px;
    }
}

@media only screen and (max-width: 480px) {
    .notfound .notfound-404 {
        height: 146px;
    }
    .notfound .notfound-404 h1 {
        font-size: 146px;
    }
    .notfound h2 {
        font-size: 16px;
    }
    .notfound .home-btn{
        font-size: 14px;
    }
}

#header{
    position: fixed;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding-bottom: 20px;
}
#header::after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #050a0e;
    max-width: calc(100% - 658px);
    z-index: -1;
}
#header .burger{
    width: 40px;
    height: 32px;
    display: none;
    overflow: hidden;
    cursor: pointer;
    position: relative;
    align-self: center;
}
#header .burger > span{
    display: block;
    position: relative;
    width: 90%;
    height: 4px;
    margin: 5px auto;
    background-color: white;
    border-radius: 10px;
}
#header .burger.active > span{
    background-color: transparent;
}
#header .burger > span#sp1{
    -webkit-transition: all .3s .5s;
    transition: all .3s .5s;
}

#header .burger > span#sp2{
    -webkit-transition: all .3s .3s;
    transition: all .3s .3s;
}

#header .burger > span#sp3{
    -webkit-transition: all .3s .1s;
    transition: all .3s .1s;
}

#header .burger.active > span#sp1{
    width: 90%;
    height: 90%;
    margin: 5px auto;
    -webkit-transition: all .3s .1s;
    transition: all .3s .1s;
}

#header .burger.active > span#sp2{
    background-color: #fff;
    -webkit-transform: translateY(-27px) rotateZ(45deg);
            transform: translateY(-27px) rotateZ(45deg);
    -webkit-transition: all .3s .3s;
    transition: all .3s .3s;
}

#header .burger.active > span#sp3{
    background-color: #fff;
    -webkit-transform: translateY(-36px) rotateZ(-45deg);
            transform: translateY(-36px) rotateZ(-45deg);
    -webkit-transition: all .3s .5s;
    transition: all .3s .5s;
}
#header .dev_menu{
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    margin-top: 61px;
    padding-right: 114px;
}
#header .dev_menu .nav_link{
    color: #FFFFFF;
    font-size: 18px;
    text-decoration: none;
    margin-right: 36px;
