}
/*---------------------------
            RESPONSIVE
-----------------------------*/
@media (max-width: 1600px) {
    #header .dev_logo{
        margin-left: 50px;
    }
    #header::after {
        max-width: calc(100% - 600px);
    }
}
@media (max-width: 1200px) {
    #header .dev_menu{
        padding-right: 50px;
    }
}
@media (max-width: 992px) {
    #header {
        position: relative;
        display: block;
        padding: 0;
    }
    #header .burger{
        display: inline-block;
    }
    #header .dev_logo {
        position: static;
        align-self: center;
        margin-left: 12px;
        margin-top: 0;
    }
    #header .logo-burger{
        display: flex;
        justify-content: space-between;
        padding: 50px 28px 0;
    }
    #header .dev_menu{
        flex-direction: column;
        position: fixed;
        width: 100%;
        align-items: center;
        background: #000000b5;
        margin-top: 0;
        top: 0;
        bottom: 0;
        padding: 50px 10px;
        display: none;
        justify-content: center;
    }
    #header .dev_menu>li:not(:last-of-type){
        margin-bottom: 8px;
    }
    #header .burger.active{
        z-index: 2;
        position: fixed;
        right: 28px;
    }
    #header .dev_menu.opened{
        display: flex;
    }
    #header .menuAnimate-enter {
        opacity: 0;
        -webkit-transform: scale(0.9);
                transform: scale(0.9);
    }
    #header .menuAnimate-enter-active {
        opacity: 1;
        -webkit-transform: translateX(0);
                transform: translateX(0);
        -webkit-transition: opacity 2000ms, -webkit-transform 2000ms;
        transition: opacity 2000ms, -webkit-transform 2000ms;
        transition: opacity 2000ms, transform 2000ms;
        transition: opacity 2000ms, transform 2000ms, -webkit-transform 2000ms;
    }
    #header .menuAnimate-exit {
        opacity: 1;
    }
    #header .menuAnimate-exit-active {
        opacity: 0;
        -webkit-transform: scale(0.9);
                transform: scale(0.9);
        -webkit-transition: opacity 2000ms, -webkit-transform 2000ms;
        transition: opacity 2000ms, -webkit-transform 2000ms;
        transition: opacity 2000ms, transform 2000ms;
        transition: opacity 2000ms, transform 2000ms, -webkit-transform 2000ms;
    }
    #header .dev_menu .nav_link{
        margin-right: 0;
    }
}

/*------------------ progress bar START-----------------*/
.progress_bar .label{
    font-size: 16px;
    color: #FFFFFF;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin-bottom: 12px;
    margin-top: 15px;
}
.progress_bar .progress_line{
    background-color: #505050;
    height: 1px;
    position: relative;
}
.progress_bar .progress_bg{
    position: absolute;
    content: '';
    display: block;
    width: 87%;
    height: 3px;
    background: #06312a;
    background: -webkit-linear-gradient(left, #06312a 0%,#0ac379 100%);
    background: linear-gradient(to right, #06312a 0%,#0ac379 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#06312a', endColorstr='#0ac379',GradientType=1 );
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.progress_bar .progress_percent{
    color: #0AC379;
    position: absolute;
    top: -25px;
    font-size: 14px;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    z-index: 1;
}
.progress_bar .progress_percent:after{
    content: '';
    width: 12px;
    height: 12px;
    background: #0AC379;
    display: block;
    margin: 3px auto 0;
    border-radius: 50%;
}
/*------------------ progress bar END-----------------*/
